.App {
  text-align: center;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.backgroundWave {
  position: fixed;
  height: 100%;
  left: 0;
  bottom: 0;
  z-index: -1;
}

.container {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 7em;
  padding: 0 2rem;
}

.bgPhoneImg {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.bgPhoneImg img{
  width: 500px;
}

.loginContainer {
  display: flex;
  align-items: center;
  text-align: center;
}

form {
  width: 360px;
}

.avatar {
  width: 100%;
} 

form h2 {
  font-size: 2.9rem;
  text-transform: uppercase;
  margin: 15px 0;
  color: #333;
}

.input-div {
  position: relative;
  display: grid;
  grid-template-columns: 7% 93%;
  margin: 25px 0;
  padding: 5px 0;
  border-bottom: 2px solid #d9d9d9;
}

.input-div:after, .input-div:before {
  content: '';
  position: absolute;
  bottom: -2px;
  width: 50%;
  height: 2px;
  background-color: #38d39f;
  transition: .3s;
}

.input-div:after {
  right: 50%;
}

.input-div:before {
  left: 50%;
}

.input-div.focus .fontAwesome awesomeFont {
  color: #38d39f;
}

.input-div.focus div h5{
  top: -5px;
  font-size: 15px;
}

.input-div.focus:after, .input-div.focus:before {
  width: 50%;
}

.input-div.one {
  margin-top: 0;
}

.input-div.one {
  margin-bottom: 4px;
}

.fontAwesome {
  display: flex;
  justify-content: center;
  align-items: center;
}

.fontAwesome.awesomeFont {
  color: #dfd3d3;
  transition: .3s;
}

.userIcon {
  color: #abadad;
  padding-bottom: 2px;
}

.lockIcon {
  color: #abadad;
  padding-bottom: 2px;
}

.input-div > div {
  position: relative;
  height: 45px;
}

.input-div > div > h5 {
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: #999;
  display: none;
  font-size: 18px;
  transition: -3s;
}

.input {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border: none;
  outline: none;
  background: none;
  padding: 0.5rem 0.7rem;
  font-size: 1.2rem;
  font-family: 'Poppins', sans-serif;
  color: #555;
}

a{
  display: block;
  text-align: right;
  text-decoration: none;
  color: #999;
  font-size: 0.9rem;
  transition: .3s;
}

a:hover{
  color: #38d39f;
}

.btn{
  display: block;
  width: 100%;
  height: 50px;
  border-radius: 25px;
  margin: 1rem 0;
  font-size: 1.2rem;
  outline: none;
  border: none;
  background-image: linear-gradient(to right, #32be8f, #38d39f, #32be8f);
  cursor: pointer;
  color: #fff;
  font-family: 'Poppins', sans-serif;
  text-transform: uppercase;
  background-size: 200%;
  transition: .5s;
}

.btn:hover{
  background-position: right;
}


/* ######### Mobile ########## */
@media screen and (max-width: 1050px){
  .container{
    grid-gap: 5rem;
  }
}

@media screen and (max-width: 1000px){
  form{
    width: 290px;
  }

  form h2{
    font-size: 2.4rem;
    margin: 8px 0;
  }

  .img img{
    width: 400px;
  }
}

@media screen and (max-width: 900px){
  .img {
    display: none;
  }

  .container {
    grid-template-columns: 1fr;
  }

  .backgroundWave {
    display: none;
  }

  .loginContainer {
    justify-content: center;
  }
}